.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.search-form{

min-height: 10vh;
display:  flex;
justify-content: center;

}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background: lightgreen;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes{

  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}